/* Skills Section */
.skills {
  flex: 1;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  overflow: hidden;
}

/* Header Styling */
.skills-header {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  font-weight: bold;
  color: aqua;
  transition: color 0.3s ease;
}



/* Skills List - Horizontal Layout */
.skills-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

/* Skill Card */
.skill-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: black;
  padding: 15px;
  border-radius: 6px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  width: 120px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.skill-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 15px rgb(136, 231, 238);
  cursor: pointer;
}

/* Skill Image */
.skill-image {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 50%;
}

/* Skill Name */
.skill-name {
  font-size: 1rem;
  color: white;
  font-weight: bold;
  margin: 10px 0;
}

/* Skill Rating */
.skill-rating {
  display: flex;
  gap: 3px;
}

.star {
  font-size: 14px;
  color: gray;
}

.star.filled {
  color: aqua;
}
